import React, { Component } from "react";
import logo from "../../img/logo.svg";
import menu from "../../img/menu.svg";
import cart from "../../img/cart.svg";
import Body from "../body/body";
class Header extends Component {
  state = {
    input: "",
    qr: false,
    contactHeader: false,
    checkDonHang: false,
    proposal: false
  };
  changeSate(value) {
    // console.log(event.target.value);
    this.setState({
      input: value,
      data: [],
    });
  }

  checkInput(event) {
    event.preventDefault();
    var form = document.getElementById("myForm");
    var data = document.getElementById("input").value;
    console.log(data);
    this.changeSate(data);
    document.getElementById("ads-product").style.display = "none";
  }
  showQR(event) {
    if (this.state.qr == false) {
      this.setState({
        qr: true,
        contactHeader: false,
        checkDonHang: false,
      });
      document.getElementById("qr").style.display = "flex";
      document.getElementById("checkDonHang").style.display = "none";
      document.getElementById("header-contact").style.display = "none";
    } else {
      this.setState({
        qr: false,
      });
      document.getElementById("qr").style.display = "none";
    }
  }
  showContactHeader(event) {
    if (this.state.contactHeader == false) {
      this.setState({
        contactHeader: true,
        qr: false,
        checkDonHang: false,
      });
      document.getElementById("header-contact").style.display = "block";
      document.getElementById("checkDonHang").style.display = "none";
      document.getElementById("qr").style.display = "none";
    } else {
      this.setState({
        contactHeader: false,
      });
      document.getElementById("header-contact").style.display = "none";
    }
  }
  showProposal(event) {
    console.log("ok");
    if (this.state.proposal == false) {
      this.setState({
        proposal: true,
      });
      document.getElementById("proposal").style.display = "block";
    } else {
      this.setState({
        proposal: false,
      });
      document.getElementById("proposal").style.display = "none";
    }
  }
  proposal(value){
    document.getElementById('input').value = value
    this.setState({
      proposal: false,
    });
    document.getElementById("proposal").style.display = "none";
  }
  showCheckDonHang(event) {
    console.log("ok");
    if (this.state.checkDonHang == false) {
      this.setState({
        checkDonHang: true,
        contactHeader: false,
        qr: false,
      });
      document.getElementById("checkDonHang").style.display = "grid";
      document.getElementById("header-contact").style.display = "none";
      document.getElementById("qr").style.display = "none";
    } else {
      this.setState({
        checkDonHang: false,
      });
      document.getElementById("checkDonHang").style.display = "none";
    }
  }
  render() {
    return (
      <>
        <div className="header">
          <div className="top-header">
            <div className="content top-header-item section .not-black-out">
              <div id="down-app" onClick={(event) => this.showQR(event)}>
                Ti ng dng
                <div className="block qr" id="qr">
                  <img src="https://media3.scdn.vn/img2/2018/5_23/R842FO.png" />
                  <span>Qut  ti ng dng</span>
                </div>
              </div>
              <div
                id="customerCare"
                onClick={(event) => this.showContactHeader(event)}
              >
                Chm sc khch hng
                <div className="block" id="header-contact">
                  <div>Trung tm h tr</div>
                  <div>Tr hng hon tin</div>
                </div>
              </div>
              <div
                className="checkDonHang"
                onClick={(event) => this.showCheckDonHang(event)}
              >
                Kim tra n hng
                <div id="checkDonHang" className="block">
                  <form>
                    <input placeholder="Nhp m n hng" className="block" />
                    <input
                      placeholder="Email / S in thoi"
                      className="block"
                    />
                    <button className="block">Kim tra</button>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <div className="bottom-header">
            <div className="bottom-header-item content section">
              <div className="logo">
                <img src={logo} />
              </div>
              <div className="form-search section">
                <div className="menu">
                  <img src={menu} />
                </div>
                <form className="search-form" id="myForm">
                  <div>
                    <input className="input-search block" id="input" onClick={(event) => this.showProposal(event)} autoComplete="off" type="search"/>
                    <div id="proposal" className="block">
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam th thao')}>giy nam th thao</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam sneaker')}>giy nam sneaker</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam cao cp')}>giy nam cao cp</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam da b')}>giy nam da b</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam trng')}>giy nam trng</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam nike')}>giy nam nike</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam c cao')}>giy nam c cao</div>
                      <hr/>
                      <div className="proposal-title">Tm kim ca hng</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam order')}>giy nam order</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('Giy Nam 2H')}>Giy Nam 2H</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('Giy Nam ep')}>Giy Nam ep</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('Giy Nam p')}>Giy Nam p</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('Giy c Nam')}>Giy c Nam</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('Giy Nam FenShoes')}>Giy Nam FenShoes</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('GIY NAM ZATA')}>GIY NAM ZATA</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('Giy Nam ROSI')}>Giy Nam ROSI</div>
                      <hr/>
                      <div className="proposal-title">Xu hng tm kim</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('tai nghe bluetooth')}>tai nghe bluetooth</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('vay tin nhanh')}>vay tin nhanh</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('gin phun vin')}>gin phun vin</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('th game garena')}>th game garena</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('qun lt n')}>qun lt n</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('my massage cm tay')}>my massage cm tay</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy li nam')}>giy li nam</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('khu trang')}>khu trang</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('my ra xe cao p')}>my ra xe cao p</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy nam')}>giy nam</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('pho hoa 36 qu')}>pho hoa 36 qu</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('qun i nam')}>qun i nam</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('my bay iu khin')}>my bay iu khin</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('giy th thao nam')}>giy th thao nam</div>
                      <div className="proposal-item" onClick={(value) => this.proposal('loa vi tnh')}>loa vi tnh</div>

                    </div>
                  </div>
                  <button
                    className="btn-search block"
                    onClick={(event) => this.checkInput(event)}
                  >
                    <svg
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                      version="1.1"
                      xlink="http://www.w3.org/1999/xlink"
                      className="svg-search"
                    >
                      <path
                        d="M10 2a8 8 0 0 1 6.32 12.905l5.387 5.388-1.414 1.414-5.388-5.386A8 8 0 1 1 10 2Zm0 2a6 6 0 1 0 0 12 6 6 0 0 0 0-12Z"
                        fill="#445058"
                        fill-rule="nonzero"
                      ></path>
                    </svg>
                  </button>
                </form>
              </div>
              <div className="sign-in section">
                <div className="cart">
                  <img src={cart} />
                </div>
                <div className="btn-sign-in">
                  <button className="block">ng nhp</button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Body test={this.state.input} />
      </>
    );
  }
}

export default Header;
